import { Box, Divider, Paper, Typography } from '@material-ui/core';
import React from 'react';
import TopNavigationRefer from '..';
import { useSelector } from 'react-redux';
import { userSelector } from '../../../../../../utils/redux/user';
import { makeStyles } from '@material-ui/styles';
import SignupButton from '../../../../../users/Create/Button';

const usePaperStyles = makeStyles({
  root: {
    width: '20rem',
    padding: '2rem',
  }
})


const ReferPopover = React.memo(function ReferPopover(props: {message?: any}){
  const paperClasses = usePaperStyles();
  const user = useSelector(userSelector);
  return <Paper classes={paperClasses}>{props.message || <Typography variant="body1">You have the <strong>maximum</strong> number of activities in your bookmarks. To allow for more activities, remove activities from your bookmarks, or click the button below</Typography>}
    <Box m="1rem 1rem"><Divider /></Box>
    {
      user ? <TopNavigationRefer /> : <SignupButton />
    }
  </Paper>
})

export default ReferPopover;