import React from 'react';
import { useSelector } from 'react-redux';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { IconButton } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import Profile from './Profile';
import FAIcon from '../../general/FAIcon';
import { userSelector } from '../../../../utils/redux/user';
import { addedActivitiesSelector, lineupActivitiesSelector } from '../../../../utils/redux/config';

import { blue } from '@material-ui/core/colors';

import TopNavigationRefer from './Refer';
import classNames from 'classnames';
import { Tooltip } from '@material-ui/core';
import SignupButton from '../../../users/Create/Button';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    fontWeight: 300,
    

  },
  logo: {
    height: '3.5rem',
  },
  titleLink: {
    flexGrow: 1,
    textDecoration: 'none',
    color: 'inherit',
    // marginRight: '3.5rem'
  },
  iconButtons: {
    // color: 'rgba(0,0,0,0.7)',
    color: blue[700],
    textDecoration: 'none'
  }
}));

export default (props: any)=>{
  const classes = useStyles();
  const user = useSelector(userSelector);
  const addedActivities = useSelector(addedActivitiesSelector);
  const lineupActivities = useSelector(lineupActivitiesSelector);

  return <AppBar position="sticky"
    color="inherit"
  >
    <Toolbar>
      <Link className={classes.titleLink} to="/">
        <Typography variant="h6" className={classNames(classes.title)}><span className="go-to-dashboard">
            {
              props.logo || "Goodplans"
            }
        </span></Typography>
      </Link>
      <Tooltip enterDelay={0} title="Bookmarks"><Link className={classNames(classes.iconButtons, 'go-to-bookmarks')} to="/activities/added"><IconButton color="inherit">{Object.keys(addedActivities).length ? Object.keys(addedActivities).length : null}<FAIcon size="md" type="bookmark"/></IconButton></Link></Tooltip>
      <Tooltip enterDelay={0} title="Activity Lineup"><Link className={classNames(classes.iconButtons, 'go-to-lineup')} to="/activities/lineup"><IconButton color="inherit">{Object.keys(lineupActivities).length ? Object.keys(lineupActivities).length : null}<FAIcon size="md" type="stream" /></IconButton></Link></Tooltip>
      <Profile user={user} className={classes.iconButtons} />
      {
        user ? <TopNavigationRefer /> : <SignupButton />
      }
    </Toolbar>
  </AppBar>
}
