import { Button, Divider, Grid, Typography } from '@material-ui/core';
import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Box } from '@material-ui/core';
import copy from 'copy-to-clipboard';
import { userSelector } from '../../../../../utils/redux/user';
import FAIcon from '../../../general/FAIcon';
import MyPopover from '../../../general/Popover';
import { Tooltip } from '@material-ui/core';
import { uiCreditsSelector, createUIAction } from '../../../../../utils/redux/ui';
import { makeStyles } from '@material-ui/styles';
import { sendEvent } from '../../../../../utils/ga';

const useStyles = makeStyles((theme: any)=>({
  icon: {
    cursor: 'pointer',
    color: theme.palette.primary.main
  }
}))

export default function TopNavigationRefer(){
  const [copied, setCopied] = React.useState(false);
  const classes = useStyles();
  const user = useSelector(userSelector);
  const credits = useSelector(uiCreditsSelector);
  const dispatch = useDispatch();
  const code = process.env.REACT_APP_CLIENT_URL + `/#/signup?referral_code=${user.id}`;
  return <MyPopover
    ga={{
      category: 'refer',
      action: 'click',
      label: 'moreAddedActivities'
    }}
    disableHover
    popoverProps={{onClose: ()=>{
      setCopied(false)
      dispatch(createUIAction({
        credits: {
          ...credits,
          hasNew: false
        }
      }))
    }}}
  renderHoverableContent={()=><Tooltip enterDelay={0} title="Referral Info"><Button variant="contained" color={credits.hasNew ? "secondary" : "primary"}>Rewards</Button></Tooltip>}
    renderOnHoverContent={()=><Box width="20rem" whiteSpace="normal" padding="1.5rem">
      {
        <Typography variant="body2">
            <strong>Add 1 more</strong> slot to your bookmarks and activity lineup everytime someone signs up with your referral code.
        </Typography>
      }
      <Box m="1rem"><Divider /></Box>
      <Box>
        <Grid container>
          <Grid item xs={3}>
            {
              copied ? "Link copied to clipboard!" : <Tooltip title="copy to clipboard">
              <span className={classes.icon} onClick={()=>{
                sendEvent({category: 'reffer', action: 'click', label: 'copy'});
                copy(code)
                setCopied(true)
              }}>
                <FAIcon 
                type="clipboard" size="2x" />
                <Typography>Copy Link</Typography>
              </span>
            </Tooltip>
            }
          </Grid>
          
          <Grid item xs={9}>
            <Typography variant="body2">{code}</Typography>
          </Grid>
        </Grid>
        {
          credits.count === 0 ? null : <Box m="1rem 0">
            <Divider />
          </Box>
        }
        {
          credits.count === 0 ? null : <Grid container><Grid item>
            <Typography variant="body2"><strong>Congratulations</strong><>, you have earned </><strong>{credits.count}</strong><>{` extra activity bookmark and lineup slot${credits.count > 1 ? 's' : ''}`}</>
            </Typography>
          </Grid></Grid>
        }
      </Box>
    </Box>
    }
  />
}