import React from 'react';
import {
    removeActivity,
    addActivityToSchedule,
    addActivity
} from '../utils';
import FAIcon from '../../../../components/general/FAIcon';
import { useSelector, useDispatch } from 'react-redux';
import {
    addedActivitySelector,
    addedActivitiesSelector,
    spaceLeftInLineupSelector
} from '../../../../../utils/redux/config';
import { IconButton } from '@material-ui/core';
import { sendEvent } from '../../../../../utils/ga';
import { uiCreditsSelector } from '../../../../../utils/redux/ui';
import MyPopover from '../../../../components/general/Popover';

import ReferPopover from '../../../../components/navigation/Top/Refer/Popover';
import { userSelector } from '../../../../../utils/redux/user';


type $props = any;


const BASE_BOOKMARKS = 3;

export default function AddActivityButtons({activity, classes, directToSchedule, disabled, spanRef}: $props){
  const credits = useSelector(uiCreditsSelector);

  const dispatch = useDispatch();
  const addedActivity = useSelector(addedActivitySelector(activity.id));
  const addedActivities = useSelector(addedActivitiesSelector);
  const spaceLeftInLineup = useSelector(spaceLeftInLineupSelector);
  const user = useSelector(userSelector);
  const allowedBookmarks = (user ? 3 : 0) + BASE_BOOKMARKS + credits.count;
  const allowMoreAddedActivities = Object.keys(addedActivities).length < allowedBookmarks;
  
  const addWrapper = allowMoreAddedActivities ? (comp: JSX.Element)=>comp : (comp: JSX.Element)=><MyPopover renderHoverableContent={()=>comp} renderOnHoverContent={()=><ReferPopover />} />
  function render(){
    return addedActivity ? <IconButton classes={classes} onClick={()=>{
      if(!disabled){
        sendEvent({category: 'schedule', action: 'click', label: 'remove'});
        dispatch(removeActivity(activity));
      }
    }} color="secondary">
      <FAIcon type="bookmark" />
    </IconButton> : 
    addWrapper(<IconButton disabled={!allowMoreAddedActivities} classes={classes} onClick={()=>{
      if(!disabled){
        sendEvent({category: 'schedule', action: 'click', label: 'add'});
        if(spaceLeftInLineup){
          dispatch(addActivityToSchedule(activity));
        } else {
          dispatch(addActivity(activity))
        }
        
      }
    }} color="primary"><FAIcon type="bookmark" /></IconButton>)
  }
  // function render(){
  //   if(directToSchedule){
  //     return addedActivity?.inSchedule ? <span className="add-to-calendar"><IconButton classes={classes} onClick={()=>{
  //       if(!disabled){
  //         sendEvent({category: 'schedule', action: 'click', label: 'remove'});
  //         dispatch(removeActivityFromSchedule(activity));
  //       }
  //     }} color="secondary">
  //       <FAIcon type="bookmark" />
  //     </IconButton></span> : 
  //     <span className="add-to-calendar"><IconButton classes={classes} onClick={()=>{
  //       if(!disabled){
  //         sendEvent({category: 'schedule', action: 'click', label: 'add'});
  //         dispatch(addActivityToSchedule(activity));
  //       }
  //     }} color="primary"><FAIcon type="bookmark" /></IconButton></span>
  //   }
  //   return addedActivity ? <span className="add-to-bookmarks"><IconButton classes={classes} onClick={()=>{
  //     if(!disabled) {
  //       sendEvent({category: 'bookmarks', action: 'click', label: 'remove'});
  //       dispatch(removeActivity(activity))
  //     }
  //   }} color="secondary">
  //     <FAIcon type="bookmark" />
  //   </IconButton></span> : 
  //   <span className="add-to-bookmarks"><IconButton classes={classes} onClick={()=>{
  //     if(!disabled){
  //       sendEvent({category: 'bookmarks', action: 'click', label: 'add'}); 
  //       dispatch(addActivity(activity))
  //     }
  //   }} color="primary"><FAIcon type="bookmark" /></IconButton></span>
  // }
  return <span style={{display: 'inline-block'}} className="add-to-bookmarks" ref={spanRef}>
    {render()}
  </span>
}