import React from 'react';

import { Divider, TextField } from '@material-ui/core';
import DiscreteSlider from './Slider';


export default function DashboardTabForm({ordinal, dashboardTab, debouncedOnChange, onChange}: any){
  return <>
    <TextField label="Tab Name" placeholder={`Tab ${ordinal+1}`} defaultValue={dashboardTab.name} onChange={(event)=>{
      debouncedOnChange(event.target.value)
    }} />
    <Divider variant="inset"/>
    <DiscreteSlider values={dashboardTab.ages} onChange={(nextAges: string[])=>onChange('ages', nextAges)} color={!dashboardTab.ages.length ? 'secondary' : undefined} />
  </>
}
