export const spaceLeftInLineupSelector = (state: $globalState)=>{
  const user = userSelector(state);
  const credits = uiCreditsSelector(state);
  const lineupActivities = lineupActivitiesSelector(state);
  return Math.max(0, (user ? 3 : 0) + BASE_ACTIVITIES_IN_LINEUP + credits.count-Object.keys(lineupActivities).length)
}
