import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Check } from '@material-ui/icons';
import { Clear } from '@material-ui/icons';
import { ListItemText, Typography } from '@material-ui/core';
import { ListItemIcon } from '@material-ui/core';
import { MenuItem } from '@material-ui/core';
import { EventAvailableRounded } from '@material-ui/icons';


import IconMenu from '../../../../components/general/IconMenu';
import {
    addCompletedActivity,
    addDroppedActivity,
    addActivityToSchedule,
    removeActivityFromSchedule
} from '../utils';
import {
    addedActivitiesSelector,
    spaceLeftInLineupSelector
} from '../../../../../utils/redux/config';
import { sendEvent } from '../../../../../utils/ga';
import MyPopover from '../../../../components/general/Popover';
import ReferPopover from '../../../../components/navigation/Top/Refer/Popover';
import { makeStyles } from '@material-ui/styles';


const useMenuItemStyles = makeStyles({
  root: {
    cursor: 'pointer !important',
        // @ts-ignore: No overload error
    pointerEvents: 'all !important',
  }
})



const AddActivityOptions = React.memo(function AddActivityOptions({activity, disabled}: {activity: any, disabled?: Boolean}){
  const menuItemClasses = useMenuItemStyles();
  const addedActivities = useSelector(addedActivitiesSelector);
  const spaceLeftInLineup = useSelector(spaceLeftInLineupSelector);
  const dispatch = useDispatch();
  return <IconMenu
    renderMenuItems={(handleClose)=>{
      return [
        addedActivities[activity.id]?.inSchedule ? <MenuItem key="add" onClick={()=>{
          if(!disabled){
            sendEvent({category: 'scheduleOptions', action: 'click', label: 'remove'});
            dispatch(removeActivityFromSchedule(activity))
          }
          handleClose()
        }}>
        <ListItemIcon>
          <EventAvailableRounded color="secondary" fontSize="small" />
        </ListItemIcon>
      <ListItemText primary="Remove From Lineup" />
    </MenuItem> : <MyPopover key="add" disableHover disablePopup={Boolean(spaceLeftInLineup)} renderHoverableContent={()=><MenuItem classes={menuItemClasses} disabled={!spaceLeftInLineup} onClick={()=>{
          if(spaceLeftInLineup){
            if(!disabled){
              sendEvent({category: 'scheduleOptions', action: 'click', label: 'add'});
              dispatch(addActivityToSchedule(activity))
            }
            handleClose()
          }
        }}>
        <ListItemIcon>
          <EventAvailableRounded color="primary" fontSize="small" />
        </ListItemIcon>
      <ListItemText primary="Add To Lineup" />
    </MenuItem>} renderOnHoverContent={()=><ReferPopover message={<Typography variant="body1">You have the <strong>maximum</strong> number of activities in your lineup. To allow for more activities, remove activities from your lineup, or click the button below</Typography>} />} />,
    <MenuItem key="done" onClick={()=>{
      if(!disabled){
        sendEvent({category: 'scheduleOptions', action: 'click', label: 'finishedActivity'});
        dispatch(addCompletedActivity(activity))
      }
      handleClose()
    }}>
        <ListItemIcon>
          <Check color="primary" fontSize="small" />
        </ListItemIcon>
      <ListItemText primary="Done" />
      </MenuItem>,
        <MenuItem key="remove" onClick={()=>{
          if(!disabled){
            dispatch(addDroppedActivity(activity))
            sendEvent({category: 'scheduleOptions', action: 'click', label: 'dropActivity'});
          }
          handleClose() 
        }}><ListItemIcon>
            <Clear color="secondary" fontSize="small" />
            </ListItemIcon>
          <ListItemText primary="Remove" />
        </MenuItem>
      ]
    }}
  />
});

export default AddActivityOptions;