import React, { cloneElement } from 'react';
import { Snackbar, IconButton } from '@material-ui/core';
import { Close as CloseIcon } from '@material-ui/icons';

import UserCreate from '../../../users/Create';
import { Switch, Route, useHistory, useLocation } from 'react-router-dom';
import SessionCreate from '../../../sessions/Create';
import SearchActivities from '../../../pages/Search';
import Dashboard from '../../../pages/Dashboard';
import ActivityShow from '../../../shedules/activities/Show';


import { useSelector, useDispatch } from 'react-redux';
import ActivityCollections from '../../../shedules/activities/added/Collections';
import ActivityLineup from '../../../shedules/activities/added/Lineup';
import Contact from '../../../pages/Contact';
import { userSelector } from '../../../../utils/redux/user';
import { globalSelector } from '../../../../utils/redux/global';
import TermsOfUse from '../../../pages/TermsOfUse';
import Privacy from '../../../pages/Privacy';

const SHEDULES = '/shedules';

export default function MainRoutes() {
  const dispatch = useDispatch();
  const {id, message} = useSelector(globalSelector).snackbar
  const handleClose = ()=>dispatch({
    snackbar: ''
  });
  return <>
    <Snackbar
      key={id}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      open={!!id}
      autoHideDuration={6000}
      onClose={handleClose}
      action={
        <IconButton size="small" aria-label="close" color="inherit" onClick={handleClose}>
          <CloseIcon fontSize="small" />
        </IconButton>
      }
      message={message}
    />
  <Switch>
    
    <Route exact path="/search"><SearchActivities /></Route>
    
    <Route exact path="/contact"><Contact/></Route>
    <Route exact path="/"><Dashboard /></Route>

    <Route exact path={`/activities/lineup`}><ActivityLineup /></Route>
    <Route exact path={`/activities/added`}><ActivityCollections /></Route>
    <Route exact path={`${SHEDULES}/activities/:sheduleId`}><ActivityShow /></Route>

    <Route exact path={`/signup`}><RouteWrapper mustBeLoggedOut><UserCreate/></RouteWrapper></Route>
    <Route exact path={`/login`}><RouteWrapper mustBeLoggedOut><SessionCreate/></RouteWrapper></Route>

    <Route exact path="/termsofuse"><TermsOfUse /></Route>
    <Route exact path="/privacy"><Privacy /></Route>

  </Switch>
  </>
};


function RouteWrapper({children, mustBeLoggedIn, mustBeLoggedOut, ...props}: {children: JSX.Element, mustBeLoggedOut?: Boolean, mustBeLoggedIn?: Boolean}){
  const history = useHistory();
  const location = useLocation();
  const user = useSelector(userSelector);
  if(mustBeLoggedOut){
    if(new RegExp('^/(signup|login).*', 'i').test(location.pathname)){
      if(user){
        history.push('/')
        return null;      
      }
    }
  }
  if(mustBeLoggedIn){
    if(!user){
      history.push('/overview')
      return null;
    }
  }
  return <>    
    {
      cloneElement(children, props)
    }
  </>
}