import React from 'react';

import { Switch, Route, useLocation } from 'react-router-dom';
import { makeStyles, Container } from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { Helmet } from 'react-helmet';
import { v4 } from 'uuid';
import Footer from '../../navigation/Footer';
import Overview from '../../../pages/Overview';
import MainRoutes from '../Main';
import TopNavigation from '../../navigation/Top';
import { SERVER_ADDRESS, get } from '../../../../utils/remote';
import { createConfigAction, configSelector } from '../../../../utils/redux/config';

import { userSelector } from '../../../../utils/redux/user';
import { createUIAction, uiCreditsSelector } from '../../../../utils/redux/ui';
import AffiliateRoutes from '../Affiliate';
import { ga, sendPageView } from '../../../../utils/ga';

const useStyle = makeStyles({
  page: {
    paddingTop: '1rem',
    paddingBottom: '4rem',
    backgroundColor: '#e6f1ff', //'#eaf5e6',//grey[100],
    minHeight: '90vh',
  }
})

const CoreApp = ()=>{
  const classes = useStyle()
  return <>
    <TopNavigation />
    <Container className={classes.page} maxWidth={false}>
      <MainRoutes />
    </Container>
  </>
}

const getUnauthenticatedId = ()=>{
  if(localStorage.getItem('unauthenticatedId')) return localStorage.getItem('unauthenticatedId');
  const newId = v4();
  localStorage.setItem('unauthenticatedId', newId);
  return newId;
}

export default function RootRoutes() {
  const dispatch = useDispatch();
  const config = useSelector(configSelector);
  const user = useSelector(userSelector);
  const credits = useSelector(uiCreditsSelector);
  const location = useLocation();

  React.useEffect(()=>{
    ga()('set', 'userId', user ? user.id : getUnauthenticatedId());
    // eslint-disable-next-line
  }, [])
  React.useEffect(()=>{
    (async ()=>{
      if(user){
        ga()('set', 'userId', user.id);
        const {data: count} = await get(SERVER_ADDRESS+'/credits/count');
        const configData = await get(SERVER_ADDRESS+'/users/config');
        dispatch(createConfigAction({
          flush: true,
          data: {
            ...config,
            ...configData
          }
        }))
        dispatch(createUIAction({
          credits: {
            count: count || 0,
            hasNew: credits.count < count,
          }
        }))
      }
    })();
  // eslint-disable-next-line
  },[user])
  React.useEffect(() => {
    window.scrollTo(0, 0);
    sendPageView(location.pathname);

  }, [location.pathname]);

    /**
   * To be removed
   */
  // React.useEffect(()=>{
  //   dispatch(createConfigAction({
  //     onboarding: {
  //       dashboard: {
  //         completed: true,
  //         step: 0,
  //       },
  //       collections: {
  //         completed: false,
  //         step: 0
  //       },
  //       lineup: {
  //         completed: false,
  //         step: 0
  //       },
  //     }
  //   }))
  // // eslint-disable-next-line
  // }, [])
  /**
   * 
   */

  return <>
      <Helmet>
        <meta charSet="utf-8" />
        <meta property="og:url"                content={window.location.pathname} />
        
      </Helmet>
      {
        process.env.REACT_APP_AFFILIATE ?  <Switch><AffiliateRoutes /></Switch> : <Switch>
          <Route exact path="/overview"><Overview/></Route>
          <Route path="*"><CoreApp /></Route>
          </Switch>
      }
    <Footer />
  </>
};


