function callWhenReady(count: number, ...args: any){
  if((window as any)?.ga?.getAll){
    for(let tracker of (window as any).ga.getAll()){
      tracker.send(...args);
    }
    return;
  }
  if(count < 50){
    setTimeout(()=>callWhenReady(count+1, ...args), 500)
  }
}

export const ga = ()=>(...args: any)=>{
  callWhenReady(0, ...args)
}
;
export default ga
export const sendEvent = (gaProps: $sendEventProps)=>{
  ga()('event', gaProps.category, gaProps.action, gaProps.label, gaProps.value)
};
export const sendPageView = (location: string)=>{
  ga()('pageview', location);
}

export type $sendEventProps = {
  category: string,
  action: string,
  label: string,
  value?: number
}