import React from 'react';
import ReactJoyride from 'react-joyride';
import tourTooltipWrapper from './Tooltip';

interface $props {
  onEnd?: Function;
  onChange?: Function;
  joyrideProps: any;
  step?: number;
  onSkip?: Function;
}

export default function Tour({onChange, onEnd, onSkip, joyrideProps, step}: $props){
  
  const onClickSkip = ()=>{
    const nextStepIndex = joyrideProps.steps.length;
    if(onSkip){
      onSkip(nextStepIndex);
    } else if(onEnd){
      onEnd(nextStepIndex);
    } else {
      onChange && onChange(nextStepIndex);
    }
  }
  const onClickNext = ()=>{
    const nextStepIndex = (step || 0) + 1;
    if(nextStepIndex === joyrideProps.steps.length && onEnd){
      onEnd(nextStepIndex);
    } else {
      onChange && onChange(nextStepIndex);
    }
  }

  return <ReactJoyride
    {...joyrideProps}
    stepIndex={step}
    // callback={handleCallback}
    tooltipComponent={tourTooltipWrapper({onClickSkip, onClickNext, steps: joyrideProps.steps})}
  />
}